import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as lendingCriteriaActions from "../../actions/lendingCriteriaActions";
import LendingCriteriaEdit from "./LendingCriteriaEdit";
import Loader from "../common/Loader";

class LendingCriteriaEditContainer extends React.Component {
  UNSAFE_componentWillMount() {
    this.props.actions.getParameterById(this.props.match.params.id);
    this.props.actions.getStateList();
    this.props.actions.getLendingTypes();
    this.props.actions.getLendingCriteriaLists();
    this.props.actions.getRecourseTypes();
    this.props.actions.getPropertyTypes();
  }

  render() {
    const {
      loading,
      lendingItem,
      stateList,
      lendingTypes,
      propTypesList,
      searchResults,
      successToast,
      maxTermTypes,
      minDscrTypes,
      maxInterestOnlyTypes,
      prePaymentPenaltyTypes,
      maxInitialTermTypes,
      maxPermAfterConstructionTypes,
      maxAmortizationTypes,
      recourseTypes,
      simpleRateSheetTypes,
    } = this.props.lendingCriteria;
    return (
      <div>
        {lendingItem &&
        lendingItem.lending_programs &&
        stateList &&
        stateList.length &&
        lendingTypes &&
        lendingTypes.length &&
        propTypesList &&
        propTypesList.length ? (
          <LendingCriteriaEdit
            id={this.props.match.params.id}
            lendingTypes={lendingTypes}
            propTypesList={propTypesList}
            stateList={stateList}
            loading={loading}
            item={lendingItem.lending_programs[0]}
            bank_name={lendingItem.bank_name}
            searchResults={searchResults}
            searchCityOrZip={this.props.actions.searchCityOrZip}
            successUpdate={successToast}
            submitUpdateLendingCriteriaForm={
              this.props.actions.updateLendingCriteria
            }
            lists={{
              maxTermTypes,
              minDscrTypes,
              maxInterestOnlyTypes,
              prePaymentPenaltyTypes,
              maxInitialTermTypes,
              lendingTypes,
              maxPermAfterConstructionTypes,
              maxAmortizationTypes,
              recourseTypes,
              simpleRateSheetTypes,
            }}
          />
        ) : (
          <Loader />
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    lendingCriteria: state.lendingCriteria,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(lendingCriteriaActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LendingCriteriaEditContainer);
