import React from "react";
import Container from "muicss/lib/react/container";
import Panel from "muicss/lib/react/panel";
import {Paper, Checkbox} from '@material-ui/core';
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Input from "muicss/lib/react/input";
import Button from "muicss/lib/react/button";
import ReactSelect from "react-select";
import Modal from "react-modal";
import { Link } from "react-router-dom";
import Textarea from "muicss/lib/react/textarea";
import Loader from "../common/Loader";
import {LENDING_CRITERIA_FIELDS} from "./config"

class LendingCriteriaEdit extends React.Component {
  state = {
    localStateArray: [],
    localPropArray: [],
    localRecourseArray:[],
    bank_name: "",
    firstLookChecked: false,
    lender_type: "",
    stateList: [],
    note: "",
    recourse: "",
    min_ltv: "",
    max_ltv: "",
    rate: "",
    fixed_floating: "",
    first_look: false,
    max_term: "",
    min_dscr: "",
    max_interest_only: "",
    pre_payment_penalty: "",
    ext_options: "",
    max_ltc: "",
    max_initial_term: "",
    max_perm_after_construction: "",
    max_amortization: "",
    index: "",
    min_index: "",
    max_index: "",
    min_rate: "",
    max_rate: "",
    "5_yr": "",
    "7_yr": "",
    "10_yr": "",
    origination_fee: "",
    max_ltv_non_recourse : "",
    local_borrower: "",
    going_in_debt_yield : "",
    going_in_dscr : "",
    stabilized_debt_yield : "",
    stabilized_dscr : "",
    showFirstLookDisclaimer: false,
    fieldsArray: [
      { col: "profile_name" },
      { col: "lender_type" },
      { col: "states" },
      { col: "property_types" },
      { col: "min_loan" },
      { col: "max_loan" },
      { col: "nationwide" },
      { col: "note" },
      { col: "recourse" },
      { col: "min_ltv" },
      { col: "max_ltv" },
      { col: "rate" },
      { col: "first_look" },
      { col: "min_rate" },
      { col: "max_rate" },
      { col: "max_term", title: "Max Term", lists: "maxTermTypes" },
      { col: "min_dscr", title: "Min DSCR", lists: "minDscrTypes" },
      {col: "max_interest_only", title: "Max Interest Only", lists: "maxInterestOnlyTypes",},
      {col: "pre_payment_penalty", title: "Pre Payment Penalty", lists: "prePaymentPenaltyTypes",},
      { col: "ext_options" },
      { col: "max_ltc" },
      {col: "max_initial_term", title: "Max Initial Term", lists: "maxInitialTermTypes",},
      {col: "max_perm_after_construction", title: "Max Perm After Construction", lists: "maxPermAfterConstructionTypes",},
      { col: "max_amortization",title: "Max Amortization",lists: "maxAmortizationTypes",},
      { col: "index" },
      { col: "min_index" },
      { col: "max_index" },
      { col: "5_yr" },
      { col: "7_yr" },
      { col: "10_yr" },
      { col: "origination_fee" },
      { col: "fixed_floating" },
      { col: "going_in_debt_yield" },
      { col: "going_in_dscr" },
      { col: "stabilized_debt_yield" },
      { col: "stabilized_dscr" },
    ],
  };

  componentDidMount() {
    this.setState({
      note: this.props.item.note,
      min_ltv: Number(this.props.item.min_ltv) !== 0 ? this.props.item.min_ltv :  "" ,
      max_ltv: Number(this.props.item.max_ltv) !== 0 ? this.props.item.max_ltv :  "" ,
      rate: this.props.item.rate,
      fixed_floating: this.props.item.fixed_floating,
      first_look: this.props.item.first_look,
      max_term: this.props.item.max_term,
      min_dscr: this.props.item.min_dscr,
      max_interest_only: this.props.item.max_interest_only,
      pre_payment_penalty: this.props.item.pre_payment_penalty,
      ext_options: this.props.item.ext_options,
      max_ltc: Number(this.props.item.max_ltc) !== 0 ? this.props.item.max_ltc :  "" ,
      max_initial_term: this.props.item.max_initial_term,
      max_perm_after_construction: this.props.item.max_perm_after_construction,
      max_amortization: this.props.item.max_amortization,
      index: this.props.item.index,
      min_loan: Number(this.props.item.min_loan) !== 0 ? this.props.item.min_loan :  "" ,
      max_loan: Number(this.props.item.max_loan) !== 0 ? this.props.item.max_loan :  "" ,
      min_index: Number(this.props.item.min_index) !== 0 ? this.props.item.min_index :  "" ,
      max_index: Number(this.props.item.max_index) !== 0 ? this.props.item.max_index :  "" ,
      min_rate: Number(this.props.item.min_rate) !== 0 ? this.props.item.min_rate :  "" ,
      max_rate: Number(this.props.item.max_rate) !== 0 ? this.props.item.max_rate :  "" ,
      "5_yr":  Number(this.props.item["5_yr"]) !== 0 ? this.props.item["5_yr"] :  "" ,
      "7_yr":  Number(this.props.item["7_yr"]) !== 0 ? this.props.item["7_yr"] :  "" ,
      "10_yr":  Number(this.props.item["10_yr"]) !== 0 ? this.props.item["10_yr"] :  "" ,
      going_in_debt_yield : Number(this.props.going_in_debt_yield) !== 0 ? this.props.item.going_in_debt_yield :  "",
      going_in_dscr : Number(this.props.going_in_dscr) !== 0 ? this.props.item.going_in_dscr :  "",
      stabilized_debt_yield : Number(this.props.stabilized_debt_yield) !== 0 ? this.props.item.stabilized_debt_yield :  "",
      stabilized_dscr : Number(this.props.stabilized_dscr) !== 0 ? this.props.item.stabilized_dscr :  "",
      origination_fee: this.props.item.origination_fee,
      lender_type: this.props.item.lender_type ? this.props.item.lender_type :
        this.props.item.lending_types.length > 0 ?
          this.props.item.lending_types.find(i => i.type_id).type_id  :  "",
      lender_type_value: this.props.item.lender_type ?
      { label: this.getLendingType( this.props.item.lender_type), value: this.props.item.lender_type } :
        this.props.item.lending_types.length > 0  ?
        {label : this.props.item.lending_types.find(i => i.type_id).value,
          value : this.props.item.lending_types.find(i => i.type_id).type_id}
       :  {label : "", value : ""},
      states: this.props.item.states.length > 0  &&
        this.props.item.states.map(i => Number(i.id)),
      states_value: this.props.item.states.length > 0  &&
        this.props.item.states
          .map((item) => ({
            value: Number(item.id),
            label: item.state,
          })),
      property_types: this.props.item.property_types.length > 0  ?
        this.props.item.property_types.map(i => Number(i.id))
        :  [],
      property_types_value: this.props.item.property_types.length > 0  ?
        this.props.item.property_types
          .map((item) => ({
            value: item.id,
            label: item.property_type,
          }))
       :  {label : "", value : ""},
      recourse: this.props.item.recourse.length > 0  ?
        this.props.item.recourse.map(i => Number(i.id))
        :  [],
      recourse_value: this.props.item.recourse.length > 0  ?
        this.props.item.recourse
          .map((item) => ({
            value: item.id,
            label: item.recourse_type,
          }))
       :  "",
      max_term_value : {label: this.props.lists.maxTermTypes
        .filter(i => i.list_type_id === Number(this.props.item.max_term))
        .map(i => i.value).toString(),value: this.props.item.max_term_value},
      min_dscr_value : {label: this.props.lists.minDscrTypes
        .filter(i => i.list_type_id === Number(this.props.item.min_dscr))
        .map(i => i.value).toString(),value: this.props.item.min_dscr_value},
      max_interest_only_value : {label: this.props.lists.maxInterestOnlyTypes
        .filter(i => i.list_type_id === Number(this.props.item.max_interest_only))
        .map(i => i.value).toString(),value: this.props.item.max_interest_only_value},
      pre_payment_penalty_value : {label: this.props.lists.prePaymentPenaltyTypes
        .filter(i => i.list_type_id === Number(this.props.item.pre_payment_penalty))
        .map(i => i.value).toString(),value: this.props.item.pre_payment_penalty_value},
      max_initial_term_value : {label: this.props.lists.maxInitialTermTypes
        .filter(i => i.list_type_id === Number(this.props.item.max_initial_term))
        .map(i => i.value).toString(),value: this.props.item.max_initial_term_value},
      max_perm_after_construction_value : {label: this.props.lists.maxPermAfterConstructionTypes
        .filter(i => i.list_type_id === Number(this.props.item.max_perm_after_construction))
        .map(i => i.value).toString(),value: this.props.item.max_perm_after_construction_value},
      max_amortization_value : {label: this.props.lists.maxAmortizationTypes
        .filter(i => i.list_type_id === Number(this.props.item.max_amortization))
        .map(i => i.value).toString(),value: this.props.item.max_amortization_value},
      localLendingArray: this.props.lendingTypes.map((item) => ({
        value: parseInt(item.list_type_id, 10),
        label: item.value,
      })),
      localPropArray: this.props.propTypesList.map((item) => ({
        value: item.list_type_id,
        label: item.value,
      })),
      localRecourseArray: this.props.lists.recourseTypes.map((item) => ({
        value: item.list_type_id,
        label: item.value,
      })),
      localStateArray: [{ value: 0, label: "Nationwide" },{ value: 0, label: "All" }].concat(
        this.props.stateList.slice(1).map((item) => ({
          value: item.id,
          label: item.state,
        }))
      ),
      bank_name: this.props.bank_name ? this.props.bank_name : "",
      firstLookChecked: this.props.item.first_look === 1,
      stateList: this.props.item.states
        ? this.props.item.states.map((s) => ({
            value: parseInt(s.id, 10),
            label: s.state,
          }))
        : [],
      nationwide: this.props.item.nationwide === 1 || false,
      profile_name: this.props.item.profile_name
        ? this.props.item.profile_name
        : "",
      },
      function () {
        if (this.state.nationwide) {
          this.setState({ stateDisabled: true });
        }
      }
    );
  }

  getLendingType (typeId){
    return this.props.lendingTypes.filter(i => i.list_type_id === typeId)
      .map(i => i.value).toString();
  }

  firstLookCheckboxChange(e) {
    if (e.target.checked) {
      this.setState({ showFirstLookDisclaimer: true });
    } else {
      this.setState({ first_look: false });
    }
  }

  acceptFirstLookDisclaimer(accept) {
    this.setState({ first_look: accept, showFirstLookDisclaimer: false });
  }
  submitForm() {
    const submitNationwide = this.state.states === "Nationwide";
    const params = {
      id: this.props.id,
      profile_name: this.state.profile_name || "",
      lender_type: this.state.lender_type,
      states: !submitNationwide && JSON.stringify(this.state.states),
      property_types: JSON.stringify(this.state.property_types),
      min_loan: this.state.min_loan || "",
      max_loan: this.state.max_loan || "",
      nationwide: submitNationwide ? 1 : 0,
      note: this.state.note || "",
      recourse: JSON.stringify(this.state.recourse),
      min_ltv: this.state.min_ltv || "",
      max_ltv: this.state.max_ltv || "",
      rate: this.state.rate || "",
      first_look: this.state.first_look ? 1 : 0,
      min_rate: this.state.min_rate || "",
      max_rate: this.state.max_rate || "",
      max_term: this.state.max_term || "",
      min_dscr: this.state.min_dscr || "",
      max_interest_only: this.state.max_interest_only || "",
      pre_payment_penalty: this.state.pre_payment_penalty || "",
      ext_options: this.state.ext_options || "",
      max_ltc: this.state.max_ltc || "",
      max_initial_term: this.state.max_initial_term || "",
      max_perm_after_construction: this.state.max_perm_after_construction || "",
      max_amortization: this.state.max_amortization || "",
      index: this.state.index || "",
      min_index: this.state.min_index || "",
      max_index: this.state.max_index || "",
      "5_yr": Number(this.state["5_yr"]) || "",
      "7_yr": Number(this.state["7_yr"]) || "",
      "10_yr": Number(this.state["10_yr"]) || "",
      origination_fee: this.state.origination_fee || "",
      fixed_floating: this.state.fixed_floating || "",
      max_ltv_non_recourse: this.state.max_ltv_non_recourse || "",
      local_borrower: this.state.local_borrower || "",
      going_in_debt_yield : this.state.going_in_debt_yield || "",
      going_in_dscr : this.state.going_in_dscr || "",
      stabilized_debt_yield : this.state.stabilized_debt_yield || "",
      stabilized_dscr : this.state.stabilized_dscr || "",
    };
    this.props.submitUpdateLendingCriteriaForm(params);
  }

  onlyDigitsAllowed(e, key) {
    this.setState({
      [key]: e.target.value.replace(/[^0-9]/g, ""),
    });
  }

  onlyDecimalsAllowed(e, key) {
    this.setState({
      [key]: e.target.value.replace(/[^0-9.]/g, ""),
    });
  }

  setSelectValue(e,key){
    this.setState({
      [key]: e.value,
      [`${key}_value`] : e
    })
  }

  setMultiSelectValue(e,key,localArray){
    if(e.map(i => i.label).includes("Nationwide")){
      this.setState({
        [key]: "Nationwide",
        [`${key}_value`] : localArray.filter(i => i.label === "Nationwide")
      })
    } else if(e.map(i => i.label).includes("All")){
      this.setState({
        [key]: localArray
          .filter(i => i.value !== 0)
          .map(i => i.value),
        [`${key}_value`] : localArray.filter(i => i.value !== 0)
      })
    } else {
      this.setState({
        [key]: e.map(i => i.value),
        [`${key}_value`] : e
      })
    }
  }

  render() {
      let lcPage = "/lending-criteria";
   console.log("this.state", this.state);
   console.log("this.props", this.props);
    return (
      <div>
        {this.props.loading ? (
          <Loader />
        ) : (
          <div>
            <div className="lc-header">
              <Container>
                <h2>{this.state.bank_name}</h2>
                <Link to={lcPage}>
                  <div className="close-circle">
                    <i className="material-icons">close</i>
                  </div>
                </Link>
              </Container>
            </div>

            <Container className="lc-container">
              <Panel className="lc-panel lc-edit-panel">
                <p className="lc-edit-panel-header">{`Edit Profile: ${this.state.bank_name} ~
                  ${this.props.item.banker_name} ~ ${this.state.lender_type_value && this.state.lender_type_value.label}`}
                </p>

                <Paper elevation={3} className="paper">
                  <Row>
                    <Col md="3" sm="6">
                      <h5>BASIC INFO:</h5>
                    </Col>
                  </Row>
                  <Row>
                    <Col sm="6">
                      <div className="ic-dropdown-container ic-dropdown-right">
                        <p className="lc-label">Lending Programs *</p>
                        <ReactSelect
                          value={this.state.lender_type_value}
                          defaultValue={this.state.lender_type}
                          onChange={item => this.setSelectValue(item, 'lender_type')}
                          options={this.state.localLendingArray}
                        />
                      </div>
                    </Col>
                    <Col sm="6">
                      {this.state.lender_type &&
                      <div className="ic-dropdown-container ic-dropdown-right">
                        <p className="lc-label">Property Types</p>
                        <ReactSelect
                          isMulti
                          closeMenuOnSelect={false}
                          value={this.state.property_types_value}
                          defaultValue={this.state.property_types}
                          onChange={item => this.setMultiSelectValue(item, "property_types", this.state.localPropArray)}
                          options={this.state.localPropArray}
                        />
                      </div>
                      }
                    </Col>
                  </Row>
                  {this.state.lender_type &&
                  <Row>
                    <Col sm="6" xs="12" className="left-column">
                      <div className="ic-dropdown-container ic-dropdown-right">
                        <p className="lc-label">State *</p>
                        <ReactSelect
                          isMulti
                          closeMenuOnSelect={false}
                          value={this.state.states_value}
                          defaultValue={this.state.states}
                          onChange={item => this.setMultiSelectValue(item, "states", this.state.localStateArray)}
                          options={this.state.localStateArray}
                        />
                      </div>
                    </Col>
                    <Col sm="3" xs="6" className="left-column">
                      <p className="lc-label">Min Loan Amount</p>
                      <Input
                        value={
                          this.state.min_loan
                            ? formatMoney(this.state.min_loan)
                            : ""
                        }
                        onChange={(e) => this.onlyDigitsAllowed(e, "min_loan")}
                        className="lc-input icon-dollar"
                      />
                    </Col>
                    <Col sm="3" xs="6" className="right-column">
                      <p className="lc-label">Max Loan Amount</p>
                      <Input
                        value={
                          this.state.max_loan
                            ? formatMoney(this.state.max_loan)
                            : ""
                        }
                        onChange={(e) => this.onlyDigitsAllowed(e, "max_loan")}
                        className="lc-input icon-dollar"
                        hint="Max"
                      />
                    </Col>
                  </Row>
                  }
                </Paper>

                <Paper elevation={3} className="paper">
                  <h5>ADVANCED INFO:</h5>
                  <Row>
                    <span>
                        <Checkbox
                          checked={this.state.first_look}
                          color="primary"
                          onChange={this.firstLookCheckboxChange.bind(this)}
                        />
                         First Look {"   "}
                      </span>
                    {LENDING_CRITERIA_FIELDS.filter(i => i.column === "local_borrower")
                      .map(j => j.sections)[0].includes(this.state.lender_type) &&
                    <span>
                          <Checkbox
                            checked={this.state.local_borrower}
                            color="primary"
                            onChange={() => this.setState({local_borrower: !this.state.local_borrower})}
                          />
                           Require a Local Borrower {"   "}
                        </span>
                    }
                  </Row>
                  {this.state.lender_type &&
                  LENDING_CRITERIA_FIELDS.filter(i => i.column === "min_rate")
                    .map(j => j.sections)[0].includes(this.state.lender_type) &&
                  <Row>
                    <Col sm="3" xs="6" className="left-column">
                      <p className="lc-label">Min Rate Amount</p>
                      <Input
                        value={this.state.min_rate}
                        onChange={(e) => this.onlyDecimalsAllowed(e, "min_rate")}
                        className="lc-input icon-percent"
                      />
                    </Col>
                    <Col sm="3" xs="6" className="right-column">
                      <p className="lc-label">Max Rate Amount</p>
                      <Input
                        value={this.state.max_rate}
                        onChange={(e) => this.onlyDecimalsAllowed(e, "max_rate")}
                        className="lc-input icon-percent"
                      />
                    </Col>
                  </Row>
                  }
                  {this.state.lender_type &&
                  LENDING_CRITERIA_FIELDS.filter(i => i.column === "index")
                    .map(j => j.sections)[0].includes(this.state.lender_type) &&
                  <Row>
                    <Col sm="6" className="left-column">
                      <div className="ic-dropdown-container ic-dropdown-right">
                        <div className="lc-label">Index</div>
                        <ReactSelect
                          value={{label: this.state.index, value: this.state.index}}
                          onChange={item => this.setSelectValue(item, "index")}
                          options={[{value: "Select an Index", label: "Select an Index"}]
                            .concat(this.props.lists.simpleRateSheetTypes.map((i) => (
                            {value: i.name, label: i.name}
                            )))}
                        />
                      </div>
                    </Col>
                    <Col sm="3" xs="6" className="left-column">
                      <p className="lc-label">Min Spread</p>
                      <Input
                        value={this.state.min_index}
                        onChange={(e) =>
                          this.setState({min_index: e.target.value})
                        }
                        className="lc-input"
                      />
                    </Col>
                    <Col sm="3" xs="6" className="right-column">
                      <div className="lc-label">Max Spread</div>
                      <Input
                        value={this.state.max_index}
                        onChange={(e) =>
                          this.setState({max_index: e.target.value})
                        }
                        className="lc-input"
                      />
                    </Col>
                  </Row>
                  }
                  {this.state.lender_type &&
                  LENDING_CRITERIA_FIELDS.filter(i => i.column === "5_yr")
                    .map(j => j.sections)[0].includes(this.state.lender_type) &&
                  <Row>
                    {[5, 7, 10].map((year) => {
                      const year_in_state = `${year}_yr`;
                      return (
                        <Col key={year} xs="4" className="right-column">
                          <p className="lc-label">{`${year} Year: `}</p>
                          <Input
                            className="lc-input"
                            value={this.state[year_in_state]}
                            onChange={(e) =>
                              this.onlyDecimalsAllowed(e, year_in_state)
                            }
                          />
                        </Col>
                      );
                    })}
                  </Row>
                  }
                  {this.state.lender_type &&
                  LENDING_CRITERIA_FIELDS.filter(i => i.column === "recourse")
                    .map(j => j.sections)[0].includes(this.state.lender_type) &&
                  <Row>
                    <Col md="6">
                      <div className="ic-dropdown-container ic-dropdown-right">
                        <p className="lc-label">Recourse</p>
                        <ReactSelect
                          isMulti
                          closeMenuOnSelect={false}
                          value={this.state.recourse_value}
                          defaultValue={this.state.recourse}
                          onChange={item => this.setMultiSelectValue(item, "recourse", this.state.localRecourseArray)}
                          options={this.state.localRecourseArray}
                        />
                      </div>
                    </Col>
                    <Col md="6">
                      <div className="ic-dropdown-container ic-dropdown-right">
                        <p className="lc-label">Rate Type</p>
                        <ReactSelect
                          value={{
                            value: this.state.fixed_floating,
                            label: ["Fixed", "Floating", "Fixed & Floating"]
                              .find((i, idx) =>(idx + 1) === this.state.fixed_floating)
                          }}
                          onChange={item => this.setSelectValue(item, "fixed_floating")}
                          options={[{value: "Select an Rate", label: "Select an Rate"}]
                            .concat(["Fixed", "Floating", "Fixed & Floating"].map((i, idx) => (
                            {value: idx + 1, label: i}
                            )))}
                        />
                      </div>
                    </Col>
                  </Row>
                  }
                  <Row>
                    {this.state.fieldsArray.filter(i => i.lists)
                      .map((field) => {
                        return (
                          this.state.lender_type &&
                          LENDING_CRITERIA_FIELDS.filter(i => i.column === field.col)
                            .map(j => j.sections)[0].includes(this.state.lender_type) &&
                          <Col md="6" key={field.col}>
                            <div className="ic-dropdown-container ic-dropdown-right">
                              <p className="lc-label">{field.title}</p>
                              <ReactSelect
                                value={this.state[`${[field.col]}_value`]}
                                defaultValue={this.state[field.col]}
                                onChange={item => this.setSelectValue(item,field.col)}
                                options={[ {value: `Select an ${field.title}`,label: `Select an ${field.title}`}]
                                  .concat(this.props.lists[field.lists].map((i) => (
                                  {value: i.list_type_id,label: i.value}
                                  )))}
                              />
                            </div>
                          </Col>
                        );
                      })}
                    {this.state.lender_type &&
                    LENDING_CRITERIA_FIELDS.filter(i => i.column === "ext_options")
                      .map(j => j.sections)[0].includes(this.state.lender_type) &&
                    <Col md="6">
                      <div className="ic-dropdown-container ic-dropdown-right">
                        <p className="lc-label">Ext. Options</p>
                        <ReactSelect
                          value={{value: this.state.ext_options, label: this.state.ext_options}}
                          defaultValue={this.state.ext_options}
                          onChange={item => this.setSelectValue(item, "ext_options")}
                          options={["Yes", "No"].map((i) => ({value: i, label: i}))}
                        />
                      </div>
                    </Col>
                    }
                    {this.state.lender_type &&
                    LENDING_CRITERIA_FIELDS.filter(i => i.column === "origination_fee")
                      .map(j => j.sections)[0].includes(this.state.lender_type) &&
                    <Col sm="3">
                      <p className="lc-label">Origination Fee</p>
                      <Input
                        className="lc-input icon-percent"
                        value={
                          this.state.origination_fee
                            ? this.state.origination_fee
                            : ""
                        }
                        onChange={(e) =>
                          this.onlyDecimalsAllowed(e, "origination_fee")
                        }
                      />
                    </Col>
                    }
                    {[
                      { col: "max_ltc", title: "Max LTC" },
                      { col: "max_ltv", title: "Max LTV" },
                    ].map((f) => {
                      return (
                        this.state.lender_type &&
                        LENDING_CRITERIA_FIELDS.filter(i => i.column === f.col)
                          .map(j => j.sections)[0].includes(this.state.lender_type) &&
                        <Col sm="3" key={f.col}>
                          <p className="lc-label">{f.title}</p>
                          <Input
                            className="lc-input icon-percent"
                            value={this.state[f.col]}
                            onChange={(e) => this.onlyDigitsAllowed(e, [f.col])}
                          />
                        </Col>
                      );
                    })}
                  </Row>
                  <Row>
                    {[
                      { col: "going_in_debt_yield",title : "Going In Debt Yield" },
                      { col: "going_in_dscr",title : "Going In DSCR" },
                      { col: "stabilized_debt_yield",title : "Stabilized Debt Yield" },
                      { col: "stabilized_dscr",title : "Stabilized DSCR" },
                    ].map((f) => {
                      return (
                        this.state.lender_type &&
                        LENDING_CRITERIA_FIELDS.filter(i => i.column === f.col)
                          .map(j => j.sections)[0].includes(this.state.lender_type) &&
                        <Col sm="3" key={f.col}>
                          <p className="lc-label">{f.title}</p>
                          <Input
                            className="lc-input icon-percent"
                            value={this.state[f.col]}
                            onChange={(e) => this.onlyDecimalsAllowed(e, [f.col])}
                          />
                        </Col>
                      );
                    })}
                  </Row>
                  {this.state.lender_type &&
                  LENDING_CRITERIA_FIELDS.filter(i => i.column === "note")
                    .map(j => j.sections)[0].includes(this.state.lender_type) &&
                  <Row>
                    <p className="lc-label">Note</p>
                      <Textarea
                        rows={2}
                        value={this.state.note}
                        className="lc-textarea"
                        onChange={(e) => this.setState({ note: e.target.value })}
                      />
                    <div
                      className="delete-note"
                      onChange={() => this.setState({ note: "" })}
                    ></div>
                  </Row>
                  }
                </Paper>
                <Paper elevation={3} className="paper" style={{ marginBottom: "40px" }}>
                  <Row>
                    <Col sm="6">
                      <Button
                        variant="flat"
                        className="save-lc-btn mui--z1"
                        onClick={() => this.submitForm(false)}
                        disabled={this.props.loading ||
                        !this.state.lender_type ||
                        (!this.state.states.length && !this.state.nationwide)
                        }
                      >
                        Save
                      </Button>
                    </Col>
                    <Col sm="6">
                      <Link to={lcPage}>
                        <Button variant="flat" className="cancel-lc-btn mui--z1">
                          Cancel
                        </Button>
                      </Link>
                    </Col>
                  </Row>
                </Paper>

              </Panel>
            </Container>

            <Modal
              className="first-look-modal"
              isOpen={this.state.showFirstLookDisclaimer}
              ariaHideApp={false}
              contentLabel="Modal"
              shouldCloseOnOverlayClick={false}
            >
              <p>
                First Look provides platinum access to view and quote all deals
                matching your lending criteria. With over 450 deals in our
                pipeline each month, First Look gives you the opportunity to do
                more business with us. Opting in to the program requires
                participants to adhere to the following rules to remain
                qualified.
                <br /> 1) Refine your Lending Criteria to match the deals you
                can quote.
                <br /> 2) You have up to 48 hours to quote or pass on each deal.
              </p>
              <div className="modal-btns">
                <Button
                  className="button btn-cancel"
                  variant="flat"
                  onClick={this.acceptFirstLookDisclaimer.bind(this, false)}
                >
                  No Thanks
                </Button>
                <Button
                  className="button"
                  variant="flat"
                  onClick={this.acceptFirstLookDisclaimer.bind(this, true)}
                >
                  I Accept
                </Button>
              </div>
            </Modal>
          </div>
        )}
      </div>
    );
  }
}
function formatMoney(cash){
  return `$${cash.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}`
}
export default LendingCriteriaEdit;
